class AppBar extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML += `

      <header class="header">
        <app-bar-logo></app-bar-logo>
        <app-bar-menu></app-bar-menu>
        <app-bar-nav id="drawer"  class="nav"></app-bar-nav>
      </header>
    `;
  }
}

customElements.define('app-bar', AppBar);
