class AppBarNav extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML += `

      <nav>
        <ul class="nav_list">
          <li class="nav_item"><a href="#/list-restaurants">Home</a></li>
          <li class="nav_item"><a href="#/favorite">Favorite</a></li>
          <li class="nav_item"><a href="https://github.com/NMSID" target="_blank">About Us</a></li>
        </ul>
      </nav>
    `;
  }
}

customElements.define('app-bar-nav', AppBarNav);
