class FootBar extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      
    `;
  }

  render() {
    this.innerHTML += `
      <footer class="footer">
        <p tabindex="0">Karest Apps | katalog restoran</p>
        <p tabindex="0">copyright &copy; 2024</p>
      </footer> 
    `;
  }
}

customElements.define('foot-bar', FootBar);
