class Hero extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML += `
      <div class="hero">
        <link rel="preload" href="./images/heros/hero-image-small.webp" as="image">
        <link rel="preload" href="./images/heros/hero-image-small.jpg" as="image">
        <link rel="preload" href="./images/heros/hero-image-large.webp" as="image">
        <link rel="preload" href="./images/heros/hero-image-large.jpg" as="image">
        <picture>
          <source media="(min-width: 600px)" srcset="./images/heros/hero-image-large.webp" type="image/webp">
          <source media="(min-width: 600px)" srcset="./images/heros/hero-image-large.jpg" type="image/jpeg">
          <source srcset="./images/heros/hero-image-small.webp" type="image/webp">
          <source srcset="./images/heros/hero-image-small.jpg" type="image/jpeg">
          <img src="./images/heros/hero-image-small.jpg" alt="Gambar Hero" class="hero_image">
        </picture>
        <h1 tabindex="0" class="hero_title">Cari makanan favoritmu di Karest</h1>
        <p tabindex="0" class="hero_tagline"
          >jelajahi kuliner di seluruh indonesia, rasakan cita rasanya sebelum
          pergi kesana</p
        >
      </div>
    `;
  }
}

customElements.define('main-hero', Hero);
